.contact_us{
    display: flex;
    flex-direction: column;
    color:darkred;
    justify-content: center;
    margin: 20px 0 10px;
    align-items: center;
} 
.Contact__Container
{
border: 1px solid white;
width: 600px;
height: flex;
flex-direction: column;
align-items: center;
justify-content: center;
padding: 30px;
margin-top: 20px;
}
.Contact__Container > p {
    font-size: 20px;
    text-align: left;
    padding-bottom: 30px;

}
.Contact__Container > p >span {
    color: blue;
}
.Contact__icon > a > button > span > .MuiSvgIcon-root{
    color: brown;
    font-size: 30px;
}
.Contact__icon > a > .MuiSvgIcon-root {
    color: orangered;
}