@import url(https://fonts.googleapis.com/css2?family=Alata&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Alata&family=Crimson+Text&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@300;600&display=swap);
*{margin: 0;}
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color:yellowgreen;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.header{
    display: flex;
    align-items: center;
    color:black;
    background-color:rgb(6, 170, 6);
    top: 0;
    position: -webkit-sticky;
    position: sticky;
    z-index: 2000;

}
.header__left {
    flex: 0.4 1;
}
.header__left > h1 {
text-align: center;
font-family: "Alata", sans-serif;
color:orangered;
font-size: 50px;
letter-spacing:1 px;
padding: 3  0px 0;

}
.header__left > h1 >span {
    color:blue;
    font-size: 30px;
}

.header__right {
    display: flex;
    flex: 0.6 1;
    justify-content: space-evenly;
    align-items: center;
}

.header__right > a > h4 {
    font-size: 25px;
    cursor: pointer;
    transition: -webkit-transform 200ms ease-in-out;
    transition: transform 200ms ease-in-out;
    transition: transform 200ms ease-in-out, -webkit-transform 200ms ease-in-out;
    font-family: "crimson text", serif;
    padding: 20px 0;
    letter-spacing: 1px;
}

.header__right > a >h4:hover {
    -webkit-transform: scale (1.09);
            transform: scale (1.09);
    background-color: #f64c08;
    color: white;
    padding: 5px 20px border-radius 2px;
    letter-spacing: 1px;
    font-family: "crimson text", serif;
    font-size: 20px;
     cursor: pointer;
}
.header__rightButton{
    background-color:orangered;
    color:white;
    padding: 5px 20px;
    border-radius: 2px;
    cursor:pointer;

}


